* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

*::before, *::after {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  line-height: 1.6;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 15px;
  font-weight: 400;
  color: #3e3e3e;
  background: #fdfdfd;
}

main {
  display: block;
}

a {
  color: #43b1b0;
  text-decoration: none;
  background-color: transparent;
  transition: color linear 250ms;
}

a:hover {
  color: #358c8b;
  text-decoration: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  padding: 0;
  border: 0;
}

.button {
  box-sizing: border-box;
  display: inline-block;
  padding: 0.8rem 1rem;
  overflow: visible;
  font-family: inherit;
  font-size: 1.5rem;
  font-weight: 400;
  line-height: 1.5;
  color: #3e3e3e;
  text-align: center;
  text-decoration: none;
  white-space: nowrap;
  cursor: pointer;
  user-select: none;
  background-color: white;
  border: 1px solid #e6e6e6;
  border-radius: 0.25rem;
  transition: background-color linear 250ms, border-color linear 250ms;
}

.button:hover, .button:focus {
  color: #3e3e3e;
  background-color: #e6e6e6;
  border-color: #cdcdcd;
  outline: 0;
}

.button.button-outline {
  background-color: transparent;
  transition: background-color linear 250ms, border-color linear 250ms, color linear 250ms;
}

